

.yetix{
    
    &--css{

        &-txt--center{  
            text-align: center;
        }

        &-d--block{
            display: block;
        }
    
        &-has-bgcolor{
            display: inline-block;
            padding: 0.375em ;
            border-radius: 0.375em;
            
            color : var( --yetix--color--black ) ;
            background-color: var( --yetix--helper--color--grey  );
        }

        &-bgcolor{
        
            &--green  { background-color: var( --yetix--helper--color--green  ) !important ; color : var( --yetix--color--white ) !important ; }
            &--orange { background-color: var( --yetix--helper--color--orange ) !important ; color : var( --yetix--color--white ) !important ; }
            &--red    { background-color: var( --yetix--helper--color--red    ) !important ; color : var( --yetix--color--white ) !important ; }
            &--blue   { background-color: var( --yetix--helper--color--blue   ) !important ; color : var( --yetix--color--white ) !important ; }
        }
    }   

    &--ticket-state{

        &--new     { background-color: var( --yetix--helper--color--green  ) !important ; color : var( --yetix--color--white )  !important ; }
        &--open    { background-color: var( --yetix--helper--color--red    ) !important ; color : var( --yetix--color--white )  !important ; }
        &--pending { background-color: var( --yetix--helper--color--blue   ) !important ; color : var( --yetix--color--white )  !important ; }
        &--hold    {  }
        &--solved  { background-color: var( --yetix--helper--color--green  ) !important ; color : var( --yetix--color--white )  !important ; }
        &--closed  {  }

        &--question{  }
        &--incident{ background-color: var( --yetix--helper--color--red    ) !important ; color : var( --yetix--color--white ) !important ; }
        &--problem {  }
        &--task    {  }
        
    }
}

