.wp-block-yetix-requestform{

  @include yetix_semantic_ui_consistency();

  .yetix-progress{
    display: none;
    margin-bottom: 1em;
  }

  .yetix-requestform-inputfile{
    display: none;
  }

  #requestform_description,
  #requestform_description * {
    font-size: var(--yetix--sui--input--font--size) !important;
    line-height: 1.21428571em !important;
  }

  > .form{

    .ui.progress .bar > .progress {
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      text-align: center;
    }
  
    .file-list .item .inner{
      display: flex;
      margin-top: 1em;
  
      .content{
        flex-grow: 1;
        align-self: center;
      }
    }

    #requestform_description, textarea {
      min-height: 7em;
    }

  }

}



/* prevent button background-color problem; ex : twentytwenty */

.mce-panel{

  --yetix--block--form--field--editor--background-color : #ffffff ;

  background-color : var( --yetix--block--form--field--editor--background-color ) !important;

  .mce-container-body button{
    color: #595959 !important;
    background-color : var( --yetix--block--form--field--editor--background-color ) !important;
  }

  .mce-btn.mce-active button,
  .mce-btn.mce-active:hover button,
  .mce-btn.mce-active i,
  .mce-btn.mce-active:hover i{
    background-color: #595959 !important;
    color : var( --yetix--block--form--field--editor--background-color ) !important; 
  }

}