

@mixin yetix_semantic_ui_consistency(){
    
    /* general */
    &, textarea{ font-size: var( --yetix--sui--font--size ); }


    /* heading */

    h1, h2, h3, h4,h5{
        margin: calc( ( var( --yetix--sui--font--size ) * 2 ) - .1428571428571429em ) 0 var( --yetix--sui--font--size ) ;
    }

    h1, .h1-like{ font-size: calc( 2          * var( --yetix--sui--font--size ) ); }
    h2, .h2-like{ font-size: calc( 1.71428571 * var( --yetix--sui--font--size ) ); }
    h3, .h3-like{ font-size: calc( 1.28571429 * var( --yetix--sui--font--size ) ); }
    h4, .h4-like{ font-size: calc( 1.07142857 * var( --yetix--sui--font--size ) ); }
    
    /* _ */

    .ui.feed,
    .ui.form,
    .ui.progress{
        font-size: var( --yetix--sui--font--size );
    }

    /* dropdown */
    select, option,
    .ui.form .field .dropdown,
    .ui.form .field .dropdown .menu > .item{
        font-size : var( --yetix--sui--dropdown--font--size ) ;
    }
 
    /* input */
    .ui.form input,
    .ui.form input:not([type]),
    .ui.form input[type="date"],
    .ui.form input[type="datetime-local"],
    .ui.form input[type="email"],
    .ui.form input[type="file"],
    .ui.form input[type="number"],
    .ui.form input[type="password"],
    .ui.form input[type="search"],
    .ui.form input[type="tel"],
    .ui.form input[type="text"],
    .ui.form input[type="time"],
    .ui.form input[type="url"]{
        font-size: var( --yetix--sui--input--font--size );
        border-radius : 0.375em;

        &:hover, &:focus, &:active{
            /* border-color: var( --yetix--color--grey ); */
            border-color: #22242659;
        }
    }

    /* checkbox */
   .ui.form input[type="checkbox"]{
        display: inline-block;
        vertical-align: middle;

        top: 0;
        width: calc( 1.5  * var( --yetix--sui--font--size ) );
        min-width: calc( 1.5  * var( --yetix--sui--font--size ) );
        height: calc( 1.5  * var( --yetix--sui--font--size ) );
        margin-right: calc( 0.5  * var( --yetix--sui--font--size ) );

        + label {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 0;
        }
    }

    .ui.form input:not([type]):focus,
    .ui.form input[type="date"]:focus,
    .ui.form input[type="datetime-local"]:focus,
    .ui.form input[type="email"]:focus,
    .ui.form input[type="file"]:focus,
    .ui.form input[type="number"]:focus,
    .ui.form input[type="password"]:focus,
    .ui.form input[type="search"]:focus,
    .ui.form input[type="tel"]:focus,
    .ui.form input[type="text"]:focus,
    .ui.form input[type="time"]:focus,
    .ui.form input[type="url"]:focus {
        border-radius : 0.375em;
    }

    /* button */
    .ui.action.input > .button:last-child,
    .ui.action.input > .buttons:last-child > .button,
    .ui.action.input > .dropdown:last-child{
        border-radius: 0 0.375em 0.375em 0;
    }

    button,
    .ui.button,
    .ui.buttons .button,
    .ui.buttons .or{
        font-size: var( --yetix--sui--button--font--size );
        border-radius : 0.375em;
    }

    .ui.buttons .button{
        border-radius: 0;
    }    

    /* toggle */
    .ui.toggle.checkbox input,
    .ui.toggle.checkbox label::before{
        width: calc( 3.5  * var( --yetix--sui--toggle--font--size ) );
        height: calc( 1.5  * var( --yetix--sui--toggle--font--size ) );
    }
    .ui.toggle.checkbox input ~ label::after {
        left: calc( -0.05  * var( --yetix--sui--toggle--font--size ) );
    }
    .ui.toggle.checkbox input:checked ~ label::after {
        left: 2.15rem;
        left: calc( 2.15  * var( --yetix--sui--toggle--font--size ) );
    }
    .ui.toggle.checkbox label {
        min-height: calc( 1.5  * var( --yetix--sui--toggle--font--size ) );
        padding-left: calc( 4.5  * var( --yetix--sui--toggle--font--size ) );;
    }
    .ui.toggle.checkbox label::after {
        width: calc( 1.5  * var( --yetix--sui--toggle--font--size ) );
        height: calc( 1.5  * var( --yetix--sui--toggle--font--size ) );
    }
    
    /* _ */
}


@mixin yetix_admin_semantic_ui_override(){

    .form-table *{
        line-height: 1.7em;
    }

    .form-table th,
    .label{
        font-weight: 500;
    }

    .ui.table .ui.accordion{
        .title.active, .content.active{ background-color: transparent; }
        .content > td{ border-top: none !important; }
    }

    .ui.checkbox input:checked ~ label::before{
        background-color: var( --yetix--color--dark-blue ) !important;
    }
    
}

@mixin yetix_semantic_ui_front_override(){



}